.greetingForm {
        display: flex;
        flex-direction: column;
}

.inputAndButtonContainer {
    display: flex; 
}

.error {
color: #C00;
font-family: Montserrat;
font-size: 14px;
font-style: normal;
font-weight: 400;
line-height: normal;
}

.input {
    display: flex;
    width: 372px;
    height: 36px;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    border-color: #D1D1D1;
}

.input:focus {
outline: none; 
border: 1px solid #D1D1D1; 
}

.errorInput {
border-color: #CC0000;
}

.button {
    display: inline-flex;
    height: 36px;
    padding: 5px 24px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    flex-shrink: 0;
    border-radius: 3px;
}

.button:focus {
background: var(--Primary-100, #06C);
}

.button:active {
background: var(--Primary-100, #06C);
}

.button:disabled {
opacity: 0.5;
background: var(--Primary-100, #06C);
}

.text {
color: rgba(0, 0, 0, 0.50);
font-family: Montserrat;
font-size: 14px;
font-style: normal;
font-weight: 400;
line-height: normal;
}

.greeting {
color: #000;
font-family: Montserrat;
font-size: 14px;
font-style: normal;
font-weight: 500;
line-height: normal;
}
