.input:focus {
border-color: #66afe9;
outline: none;
}

.input {
display: flex;
width: 372px;
height: 36px;
justify-content: center;
align-items: center;
flex-shrink: 0;
border: 1px solid #ccc;
padding: 10px;
font-size: 16px;
border-radius: 4px;
}

.inputWrapper {
margin-bottom: 10px;
}

.superInput {
border: 1px solid #212020;
width: 372px;
height: 36px;
padding: 12px;
font-size: 18px;
border-radius: 6px;
}

.errorInput {
border: 1px solid #e74c3c;
}

.errorInput:focus {
border-color: #e74c3c;
outline: none;
}

.error {
color: #e74c3c;
font-size: 14px;
margin-top: 5px;
}
