.button {
display: inline-flex;
padding: 5px 24px;
justify-content: center;
align-items: center;
gap: 10px;
}

.disabled {
border-radius: 3px;
opacity: 0.5;
background: #004D99;
}

.secondary {
display: inline-flex;
padding: 5px 24px;
justify-content: center;
align-items: center;
gap: 10px;
}

.default {
display: inline-flex;
padding: 5px 24px;
justify-content: center;
align-items: center;
gap: 10px;
background-color: #f0f0f0;
}

.red {
display: inline-flex;
padding: 5px 24px;
justify-content: center;
align-items: center;
gap: 10px;
border-radius: 3px;
background: var(--Danger-100, #CC1439);
}

.default:hover {
    background-color: #e0e0e0; 
}

.red:hover {
    background-color: #b3122f;
}

.secondary:hover {
    background-color: #d0d0d0;
}


.default:active {
    background-color: #d0d0d0;
}

.secondary:active {
    background-color: #c0c0c0;
}

.red:active {
    background-color: #b3122f;
}

.button:disabled {
    cursor: not-allowed; 
    opacity: 0.5; 
}
