.sidebar {
    width: 317px;
    height: 720px;
    flex-shrink: 0;
}

.sidebar.open {
width: 317px;
height: 720px;
flex-shrink: 0;
}

.background {
background: #FFF;
box-shadow: 0px 10px 40px 0px rgba(29, 33, 38, 0.13), 0px 1px 2px 0px rgba(29, 33, 38, 0.10);
}

.nav {
    display: flex; 
    flex-direction: column;
    margin-left: 70px;
    margin-bottom: 45px;
}

.nav a {
    color: #000;
    font-family: Montserrat;
    font-size: 22px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}

.nav a:hover {
color: #58ACFF;
font-family: Montserrat;
font-size: 22px;
font-style: normal;
font-weight: 600;
line-height: normal;
}

.nav a.active {
    color: #0059B2;
    font-family: Montserrat;
    font-size: 22px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    text-decoration-line: underline;
}

.close {
display: flex;
width: 24px;
height: 24px;
justify-content: center;
align-items: center;
flex-shrink: 0;
}

.close img {
display: none; 
}
